<template>
  <table id="door-table">
    <thead></thead>

    <th v-for="door in doors" :key="door.doorID">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
        aria-hidden="true"
        focusable="false"
        width="6em"
        height="6em"
        style="-ms-transform: rotate(360deg); -webkit-transform: rotate(360deg); transform: rotate(360deg);"
        preserveAspectRatio="xMidYMid meet"
        viewBox="0 0 24 24"
      >
        <path v-if="door.doorStatus == true" :d="svgOpen" :fill="svgOpenColor" />
        <path v-else-if="door.doorStatus == false" :d="svgClosed" :fill="svgClosedColor" />
      </svg>

      <tr>
        Door nr: {{door.doorID}}
        <br />
        <DoorButton :doorStatus="door.doorStatus" :doorID="door.doorID" @clickedDoor="changeStatus"></DoorButton>
      </tr>
    </th>
    <tbody></tbody>
  </table>
</template>

<script>
import DoorButton from "./DoorButton.vue";
import { eventBus } from "../eventbus";
export default {
  name: "AllDoor",
  components: { DoorButton },
  methods: {
    changeStatus: function(doorID) {
      let action = "";
      if (this.doors[doorID].doorStatus) {
        this.$http
          .post("/change/" + doorID + "/close")
          .then(() => {
            this.doors[doorID].doorStatus = !this.doors[doorID].doorStatus;
          });
      } else {
        this.$http
          .post("/change/" + doorID + "/open")
          .then(() => {
            this.doors[doorID].doorStatus = !this.doors[doorID].doorStatus;
          });
      }
    }
  },
  props: ["doors", "groups"],
  data: function() {
    return {
      svgClosed:
        "M16 11h2v2h-2v-2zm-4-8h7c1.108 0 2 .892 2 2v14h1v2H2v-2h8V5c0-1.108.892-2 2-2zm0 2v14h7V5h-7z",
      svgClosedColor: "#e59c00",
      svgOpen:
        "M12 3c-1.108 0-2 .892-2 2H3v14H2v2h20v-2h-1V5c0-1.108-.892-2-2-2h-7zm0 2h7v14h-7V5zm-7 6h2v2H5v-2z",
      svgOpenColor: "#27ae60"
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
th {
  padding: 10px;
  font-size: 14px;
}
</style>


