<template>
  <div class="pure-u-1-3">
    <div class="container">
      <h1>Door Groups</h1>

      <Alldoor class="doorList" :doors="doorList" @clickedGroup="fetchDoorList" ></Alldoor>

      <DoorsInGroup
        v-for="group in groups"
        :key="group.groupID"
        :doors="doorList"
        :group="group"
        :groupName="group.groupID"
        :groupDoors="group.doorIdList"
      ></DoorsInGroup>
    </div>
    <div class="pure-u-1-3"></div>
  </div>
</template>
<script>
import Alldoor from "./Alldoor.vue";
import DoorsInGroup from "./DoorsInGroup.vue";
import { eventBus } from "../eventbus";
export default {
  name: "EventList",
  components: { Alldoor, DoorsInGroup },
  data: function() {
    return {
      groups: [],
      doorList: [],
    };
  },

  created: function() {
    this.fetchDoorList();
    this.fetchGroupList();
    //this.timer = setInterval(1000);
  },
  mounted: function() {
    eventBus.$on("clickedGroup", msg => {
      console.log("Control group pressed");
     this.fetchDoorList();
    });
  },

  methods: {
    fetchDoorList: function() {
      this.$http.get("/doorlist").then(result => {
        this.doorList = [];
        result.data.map(d => {
          this.doorList.push({ doorID: d.doorID, doorStatus: d.doorStatus });
        });
      });
    },
    fetchGroupList: function() {
      this.$http.get("/grouplist").then(result => {
        result.data.map(d => {
          this.groups.push({
            groupID: d.groupID,
            groupStatus: d.groupStatus,
            doorIdList: d.doorIdList
          });
        });
      });
    },
    cancelAutoUpdate: function() {
      clearInterval(this.timer);
    }
  },
  beforeDestroy() {
    clearInterval(this.timer);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.doorList {
  position: relative;
  margin-left: 200px;
}

.container {
  width: 1200px;
  text-align: center;
}
</style>



