
<template>
  <multiselect
    v-model="myVal"
    :options="doors"
    :multiple="true"
    :close-on-select="false"
    :custom-label="customLabel"
    @select="selectedDoor"
    :clear-on-select="true"
    placeholder="groupDoors"
    label="doorID"
    track-by="doorID"
    @remove="unselectedDoor"
  >
    <template slot="selection" label="doorID" slot-scope="{ isOpen }"></template>
  </multiselect>
</template>


<script>
import Multiselect from "vue-multiselect";

export default {
  components: {
    Multiselect
  },
  props: ["doors", "doorList", "groupID"],
  data() {
    return {
      newArray: [],
      value: null
    };
  },
computed : {
     myVal: {
       get() {  return this.doorList },

        set(newValue) {}
     },
  },
 
  methods: {
    selectedDoor(door) {
      if (door) {
        this.$http.post(
          "/assign/" + this.groupID + "/" + door.doorID
        );
        this.$emit("clicked", this.groupID);
      }
    },
    customLabel({doorID}){
      return 'Door ' + doorID
    },

    unselectedDoor(door) {
      if (door) {
        this.$http.post(
          "/remove/" + this.groupID + "/" + door.doorID
        );
        this.$emit("clicked", this.groupID);
      }
    }
  }

};
</script>

<style>

.multiselect__tag,
.multiselect__tag-icon:hover,
.multiselect__option--highlight
{background:grey}
/* ///////////////////////// */



</style>



