<template>
  <div class="box" v-bind:class="[boxNumber]">
    <h1>Koridorius {{groupName}}</h1>

    <DoorSelector
      :doors="doors"
      :doorList="doorList"
      :groupID="group.groupID"
      @clicked="getDoorList"
    ></DoorSelector>
    {{error}}
    <br />
    <button
      class="btn"
      v-bind:class="{orange: isActive}"
      @click="changeStatus(group.groupID, group.groupStatus)"
    >{{buttonname(group.groupStatus)}}</button>
  </div>
</template>

<script>
import DoorSelector from "./DoorSelector.vue";
import {eventBus} from '../eventbus'
export default {
  name: "DoorsInGroup",
  components: { DoorSelector },
  props: ["group", "groupName", "doors"],
  data: function() {
    return {
      boxNumber: "first",
      isActive: true,
      isBefore: true,
      first: true,
      firstGroup: [],
      secondGroup: [],
      doorList: [],
      error: ""
    };
  },

  created: function() {
    this.isActive = !this.group.groupStatus;

    if (this.group.groupID == "1") {
              
      this.boxNumber = "first";
    } else {
      this.boxNumber = "second";
    }
console.log(this.boxNumber)
  },
  mounted() {
    //this.formatList();
    this.getDoorList(this.group.groupID);
  },
  methods: {
    changeStatus: function(groupID, groupStatus) {
   
      var status;
      if (groupStatus) {
        status = "close";
      } else {
        status = "open";
      }
      this.$http
        .post("/controlgroup/" + groupID + "/" + status)
        .then(
                
          () => {
            this.error = "";
            this.isActive = !this.isActive;
            this.group.groupStatus = !this.group.groupStatus;
            eventBus.$emit('clickedGroup', this.group.groupID);
            console.log("emitted groupctntlrl message");

          },
          err => {
            this.error = alert("Another group is already opened!!!");
          }
        );
    },
    getDoorList: function(groupID) {
      this.$http
        .get("/groupdoorlist/" + groupID)
        .then(result => {
          this.doorList = [];
          result.data.map(d => {
            this.doorList.push({ doorID: d });
          });
        });
    },

    buttonname: function(groupStatus) {
      if (groupStatus) {
        return "Close Group";
      } else {
        return "Open Group";
      }
    },

    formatList() {
      const newList = [];
      this.group.doorIdList.map(item => {
        this.newDoorList.push({ doorID: item });
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.btn {
  position: relative;
  display: inline;
  margin: 16px 8px;
  padding: 5px;
  overflow: hidden;
  border-width: 0;
  outline: none;
  border-radius: 2px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.6);
  background-color: #2ecc71;
  color: #ffffff;
  transition: background-color 0.3s;
}

.btn > * {
  position: relative;
}
.btn:hover,
.btn:focus {
  background-color: #27ae60;
}
.btn:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 0;
  padding-top: 0;
  border-radius: 100%;
  background-color: rgba(236, 240, 241, 0.3);

  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.btn:active:before {
  width: 120%;
  padding-top: 120%;

  transition: width 0.2s ease-out, padding-top 0.2s ease-out;
}
.btn.orange {
  background-color: #e67e22;
}

.btn.orange:hover,
.btn.orange:focus {
  background-color: #d35400;
}

.btn.red {
  background-color: #e74c3c;
}

.btn.red:hover,
.btn.red:focus {
  background-color: #c0392b;
}
.container {
  width: 1100px;
  text-align: center;
}

.box.first {
  float: left;
  padding: 10px;
}

.box.second {
  float: right;
  padding: 10px;
}

</style>



