<template>
  <button
    class="btn"
    v-bind:class="{orange: !doorStatus, before: !doorStatus}"
    v-on:click="buttonClicked"
  >  <span class="close">{{doorStatus ? "Close": "Open"}}</span>
  </button>
</template>

<script>
import { eventBus } from "../eventbus";
export default {
  name: "DoorButton",
  props: ["doorStatus", "doorID"],
  data: function() {
    return { isActive: false, isBefore: false };
  },

  methods: {
    buttonClicked: function(){
    this.$emit("clickedDoor", this.doorID);
    }
  }
};
</script>
<style scoped>
.btn {
  position: relative;
  display: inline;
  margin: 8px;
  padding: 3px 10px;
  overflow: hidden;
  border-width: 0;
  outline: none;
  border-radius: 2px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.6);
  background-color: #2ecc71;
  color: #ecf0f1;
  transition: background-color 0.3s;
}

.btn > * {
  position: relative;
}
.btn:hover,
.btn:focus {
  background-color: #27ae60;
}
.btn:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width:0;
  padding-top: 0;
  border-radius: 100%;
  background-color: rgba(236, 240, 241, 0.3);

  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.btn:active:before {
  width: 100%;
  padding-top: 100%;

  transition: width 0.2s ease-out, padding-top 0.2s ease-out;
}
.btn.orange {
  background-color: #f19500;
}

.btn.orange:hover {
  background-color: #d35400;
}


.btn.red:hover,
.btn.red:focus {
  background-color: #c0392b;
}


</style>





